.link {
  text-decoration:none;
  color:#fff;
}

.item {
  width:100%;
  padding:24px;
  box-sizing:border-box;
  background: #1C1C21;
  box-shadow:0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06), 0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  margin-bottom:16px;
}

.header {
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}

.img_container {
  position:relative;
  display:flex;
  flex-direction:row;
  list-style-type:none;
  padding:0;
}

.img_item {
  position:relative;
  width:64px;
  height:64px;
  background:#131316;
  border:2px solid rgba(76, 76, 255, 1);
  border-radius:100px;
}

.img_item:not(:first-child) {
  margin-left: -16px;
}

.img_item img, .img_item_col img {
  height: 64px;
  width: 64px;
  object-fit: cover;
  border-radius: 50%;
}

.img_item_col {
  position:relative;
  width:64px;
  height:64px;
  margin-left: -16px;
}

.img_item_cola {
  background:#131316;
  border:2px solid rgba(76, 76, 255, 1);
  position:absolute;
  top:0;
  left:0;
  bottom:0;
  right:0;
  opacity:0.6;
  border-radius:100px;
}
.col {
  position:relative;
  display:inline-block;
  line-height:64px;
  width:100%;
  text-align:center;
}

.footer {
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}

.price {
  display:flex;
  align-items:center;
}

.status {
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}

.status_item {
  width:272px;
}

.list {
  list-style-type: none;
  margin-left:0;
  padding-left:0;
  display: flex;
  flex-direction:column;
  column-gap:18px;
  max-height:350px;
  flex-wrap: wrap;
  overflow:hidden;
}

.list li {

}


.ts {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25), 0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

